}

.miniMenuItem {
  text-decoration: none;
  background-color: white;
  text-align: center;
  font-family: Chicago, sans-serif;
  width: 100%;
  font-size: 1.2em;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 255, 0.2);
  padding: 2px;
}

.miniMenuItemDiv a:hover {
  color: white;
  background-color: #6764fd;
}

.miniMenuItemDiv {
  border-bottom: 2px solid black;
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 24px;
}
.miniMenuItemDiv:hover {
  background-color: #6764fd;
}
.miniMenuItemDiv:last-child {
  border-bottom: transparent;
}
